:root {
  --button-green: #bbf330;
  --dark-green: #7eb101;
  --white: #fefefe;
  --yellow-main: #ffe550;
  --black: #2c2c2c;
  --shadow-fruit: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --transition: .4s ease;
}

body {
  color: var(--black);
  font-family: Roboto;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.29;
}

.container {
  max-width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background: var(--dark-green);
  border: 1px solid var(--white);
  border-radius: 20px;
}

body::-webkit-scrollbar-track {
  background-color: var(--yellow-main);
}

@media screen and (max-width: 767.98px) {
  .container {
    max-width: 320px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

